"use client";

import { useEffect } from "react";
import { useAuth } from "@/contexts/auth-context";
import { Button } from "@/components/ui/button";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import { Loader2 } from "lucide-react";
import { useRouter } from "next/navigation";
import { isConfigValid } from "@/lib/firebase";

const DEV_MODE = process.env.NEXT_PUBLIC_DEV_MODE === "true";

export default function LoginPage() {
  const { user, loading, signInWithGoogle } = useAuth();
  const router = useRouter();

  useEffect(() => {
    // If user is already logged in, redirect to trainings
    if (user && !loading) {
      router.push("/trainings");
    }
  }, [user, loading, router]);

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center" dir="rtl">
        <Loader2 className="h-8 w-8 animate-spin text-primary" />
      </div>
    );
  }

  if (user) {
    return null; // Will redirect
  }

  return (
    <div
      className="min-h-screen flex items-center justify-center bg-gradient-to-br from-background to-secondary p-4"
      dir="rtl"
    >
      <Card className="w-full max-w-md animate-fade-in">
        <CardHeader className="text-center space-y-2">
          <CardTitle className="text-3xl font-bold">ברוכים הבאים</CardTitle>
          <CardDescription className="text-base">
            התחבר כדי להמשיך לאוצר מילים
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          {DEV_MODE && (
            <div className="bg-blue-500/10 border border-blue-500/20 rounded-lg p-4 text-sm text-blue-600 dark:text-blue-400 text-center">
              <strong>מצב פיתוח פעיל</strong>
              <br />
              לחץ על הכפתור למטה כדי להשתמש במשתמש דמה
            </div>
          )}

          {!DEV_MODE && !isConfigValid && (
            <div className="bg-destructive/10 border border-destructive/20 rounded-lg p-4 text-sm text-destructive text-center">
              Firebase לא מוגדר. אנא עיין בקובץ FIREBASE_SETUP.md להוראות התקנה.
            </div>
          )}

          <Button
            onClick={signInWithGoogle}
            size="lg"
            className="w-full h-14 text-lg rounded-xl shadow-lg hover:shadow-xl transition-all duration-200"
            disabled={!DEV_MODE && !isConfigValid}
          >
            <svg className="ml-2 h-5 w-5" viewBox="0 0 24 24">
              <path
                fill="currentColor"
                d="M22.56 12.25c0-.78-.07-1.53-.2-2.25H12v4.26h5.92c-.26 1.37-1.04 2.53-2.21 3.31v2.77h3.57c2.08-1.92 3.28-4.74 3.28-8.09z"
              />
              <path
                fill="currentColor"
                d="M12 23c2.97 0 5.46-.98 7.28-2.66l-3.57-2.77c-.98.66-2.23 1.06-3.71 1.06-2.86 0-5.29-1.93-6.16-4.53H2.18v2.84C3.99 20.53 7.7 23 12 23z"
              />
              <path
                fill="currentColor"
                d="M5.84 14.09c-.22-.66-.35-1.36-.35-2.09s.13-1.43.35-2.09V7.07H2.18C1.43 8.55 1 10.22 1 12s.43 3.45 1.18 4.93l2.85-2.22.81-.62z"
              />
              <path
                fill="currentColor"
                d="M12 5.38c1.62 0 3.06.56 4.21 1.64l3.15-3.15C17.45 2.09 14.97 1 12 1 7.7 1 3.99 3.47 2.18 7.07l3.66 2.84c.87-2.6 3.3-4.53 6.16-4.53z"
              />
            </svg>
            {DEV_MODE ? "התחבר (מצב פיתוח)" : "התחבר עם Google"}
          </Button>

          <div className="text-center text-sm text-muted-foreground">
            על ידי התחברות, אתה מסכים לתנאי השימוש ולמדיניות הפרטיות
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
